import React from 'react';
import PropTypes from 'prop-types';
import Layout from 'components/layout';
import Box from 'components/box';
import Title from 'components/title';
import Gallery from 'components/gallery';
import Projects from 'components/projects';
import IOExample from 'components/io-example';
import Resume from 'components/resume';
import Modal from 'containers/modal';
import { graphql } from 'gatsby';

const Index = ({ data }) => (
  <Layout>
    <div className="intro-wrapper">
      <h1>
        Hi, {`I'm`} <span className="strong">Edu.</span>
      </h1>
      <div className="intro-box">
        <p className="me-into-p">
          {`I understand people who have `}
          <strong className="double-strong">problems</strong>
          {` and people who have the power to solve them. When they don't sync well I try to communicate, guide and negotiate to bridge the gaps.`}
        </p>
        <p className="intro-footer">
          {`If everything's fine, you can find me `} <i>racing motorcycles</i>
        </p>
        {/*<Modal>
          <video
            src="https://i.imgur.com/gzFqNSW.mp4"
            playsInline
            loop
            autoPlay
            muted
          />
        </Modal>*/}
      </div>
    </div>
    <div>
      {/*<div style={{ height: '50vh' }} />
      <IOExample />
    <Gallery items={data.homeJson.gallery} />*/}
      <Projects items={data.homeJson.projects} />
    </div>

    <div id="aboutContainer" className="about-wrapper">
      <div className="about-desc">
        <div className="about-head">Project overview</div>
        <div className="about-desc-left">
          <p>
            In India, the information technology industry has shown a steadily
            increasing trend from 1998. It never had a downfall and is still in
            its growth path even today. This also led to an increased number of
            developers being hired. However, the selection of a candidate for
            being hired into a software building company was mostly through
            campus hires and referrals.
          </p>
          <p>
            This has not been in favour of the employer because they might miss
            out on the right candidate as well as the candidate because he/she
            would have missed a good opportunity just because he was not from
            the tier 1 colleges. This is where HackerEarth intervenes to make
            both ends meet.
          </p>
        </div>
      </div>

      <Resume />
    </div>
  </Layout>
);

Index.propTypes = {
  data: PropTypes.object.isRequired,
};

export default Index;

export const query = graphql`
  query HomepageQuery {
    homeJson {
      title
      content {
        childMarkdownRemark {
          html
          rawMarkdownBody
        }
      }
      gallery {
        title
        copy
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
      projects {
        title
        desc
        date
        image {
          childImageSharp {
            fluid(maxHeight: 500, quality: 90) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        link
      }
    }
  }
`;
